import React from 'react';
import './Contact.css';

const Contact = () => {
    return (
      <div id="contact" className='white container section-middle'>
        <h1 className="f1 pa3">Contact Us</h1>
        <hr />
         <p className="f3">
            {`Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor 
            incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud 
            exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure 
            dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. 
            Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt 
            mollit anim id est laborum.`}
          </p>
          <form method="post" className="contact-form">
            <p className="f3">
              <label>Name:</label>
              <input type="text" name="name" required/>
            </p>
            <p className="f3">
              <label>Email:</label>
              <input type="email" name="email" required/>
            </p>
            <p className="f3">
              <label>Phone number:</label>
              <input  type="text" name="phone" required/>
            </p>
            <p className="f3">
              <label>Budget:</label>
                <select name="budget">
                    <option value="">Select</option>
                    <option value="25">Less than 25k</option>
                    <option value="25-50">25-50k</option>
                    <option value="50-100">50-100k</option>
                    <option value="100+">More than 100k</option>
                    <option value="Not Sure">Not Sure</option>
                </select>
            </p>
            <p className="full f3">
              <label>Tell us about your project:</label>
              <textarea name="description" rows="5" required></textarea>
            </p>
            <p className="full grow f3">
              <input className="submitBtn" type="submit" value="Submit" />
            </p>
            
            
          </form>
      </div>
      
      );
}

export default Contact;