import React, { Component } from 'react';
import './Navigation.css';
import logo from './logo.png';
import menu from './menu.png';
import { Link, animateScroll as scroll } from 'react-scroll';

const speed = 300;
const workText = 'Our Work';
const servicesText = 'What We Do';
const aboutText = 'Who We Are';
const contactText = 'Contact Us';


export default class Navigation extends Component {
  scrollToTop = () => {
    scroll.scrollToTop();
  };
  
  render() {
    return (
      <nav className="nav shadow-5" id="navbar">
        <div className="nav-content">
          <img
            src={logo}
            className="nav-logo"
            alt="Logo."
            onClick={this.scrollToTop}
          />
          <ul className="nav-items">
          <li className="nav-item noselect">
              <Link activeClass="active" to="work" spy={true} smooth={true} duration={speed}>
                {workText}
              </Link>
            </li>
            <li className="nav-item noselect">
              <Link activeClass="active" to="services" spy={true} smooth={true} duration={speed}>
                {servicesText}
              </Link>
            </li>
            <li className="nav-item noselect">
              <Link activeClass="active" to="about" spy={true} smooth={true} duration={speed}>
                {aboutText}
              </Link>
            </li>
            <li className="nav-item noselect">
             <Link activeClass="active" to="contact" spy={true} smooth={true} duration={speed}>
                {contactText}
              </Link>
            </li>
            
          </ul>
        </div>
      </nav>
    );
  }
}



