import React from 'react';
import './DemoReel.css';

const vimeoPlayer = 'https://player.vimeo.com/api/player.js';
const playerColor = '990000';
const videoID = '301883010';
const videoURL = `https://player.vimeo.com/video/${videoID}`;
const iFrameSource = `${videoURL}?color=${playerColor}&title=0&byline=0&portrait=0`;

const DemoReel = () => {
    return (
      
        <div id="container-block">
          <div class="vimeo-wrapper shadow-5">
                <iframe title="demo" src={iFrameSource} frameborder="0"
                webkitallowfullscreen="true" mozallowfullscreen="true" allowfullscreen="true"></iframe>
            </div>
            <script src={vimeoPlayer}></script>
        </div>
      
      );
}

export default DemoReel;